module.exports = "/**\n * Applies styles for users in high contrast mode. Note that this only applies\n * to Microsoft browsers. Chrome can be included by checking for the `html[hc]`\n * attribute, however Chrome handles high contrast differently.\n * @param target Which kind of high contrast setting to target. Defaults to `active`, can be\n *    `white-on-black` or `black-on-white`.\n */\n/* Theme for the ripple elements.*/\n/* stylelint-disable material/no-prefixes */\n/* stylelint-enable */\n/****** \n    App Bar \n******/\n/* transparent navigation bar */\n.mat-toolbar.mat-toolbar-multiple-rows {\n  position: fixed;\n  z-index: 1000;\n  /* Ensure that your app's content doesn't overlap the toolbar. The z-index property specifies the stack order of an element. */\n  background-color: rgba(0, 0, 0, 0.2);\n  min-height: initial;\n  /* override height to adapt content. default 64px */ }\n.mat-toolbar-row {\n  height: 50px;\n  /* toolbar height. default 64px. Dependency: bigger than logo & nav tabs */ }\n/* logo & tab */\nnav.mat-tab-nav-bar,\nheader .logo-wrapper {\n  border-bottom: transparent;\n  /* transparent bottom border (inactive items underline) */\n  background-color: rgba(255, 255, 255, 0.7);\n  border-radius: 3px; }\n/****** \n    Logo \n******/\n.logo-wrapper {\n  color: dimgray; }\n/* for banner */\nheader .logo-wrapper {\n  min-height: 40px;\n  /* logo button's height. (logo will resize to fit) */\n  min-width: 80px;\n  /* logo button's width. (logo will resize to fit) */ }\n/* for drawer */\n::ng-deep mat-sidenav .logo-wrapper {\n  min-height: 50px; }\n/****** \n    Tab \n******/\n/* tab ink bar */\n::ng-deep nav.mat-tab-nav-bar .mat-tab-links mat-ink-bar.mat-ink-bar {\n  background-color: black; }\n/* tab buttons */\n::ng-deep.mat-tab-link[mat-tab-link] {\n  height: 40px;\n  /* reduce tab button height here. Default 48px. */\n  padding: 0 17px;\n  /* reduce tab button width here. Default 0 24px. */\n  min-width: 0px;\n  /* reduces tab button width here */\n  font-size: 0.7rem;\n  /* reduces tab font size here */ }\n/* for drawer */\n::ng-deep mat-sidenav div [mat-button] {\n  padding: 1.5rem 0;\n  width: 12rem; }\n\n/*# sourceMappingURL=data:application/json;base64, */"